import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export default function Imgslide({ projectName, num }) {
    var settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      arrows: false,
      autoplay:true,
      autoplaySpeed:2500,
      centerMode: true,
      centerPadding: "0px",
    };
  
    const divTags = [];
  
    for (let i = 1; i <= num; i++) {
      const imagePath = `/imgs/${projectName}/${i}.jpg`;
      divTags.push(
        <div key={i}> 
          <img src={`${process.env.PUBLIC_URL}/imgs/${projectName}/${i}.jpg`} alt={`Image ${i}`} style={{margin:'auto'}}/>
        </div>
      );
    }
  
    return (
      <Slider {...settings}>
        {divTags}
      </Slider>
    );
  }