import React, { useEffect, useState } from 'react';
import './bubble.css'; // 위의 CSS를 포함한 스타일 시트

const Bubble = () => {
  const [isVisible, setIsVisible] = useState(false);
  const [screenHeight, setScreenHeight] = useState(window.innerHeight);

  useEffect(() => {
    const handleResize = () => {
      setScreenHeight(window.innerHeight);
    };

    // 페이지 로드 시와 창 크기 변경 시에 화면의 높이를 다시 계산합니다.
    handleResize();
    window.addEventListener('resize', handleResize);

    // 컴포넌트가 언마운트될 때 리스너를 제거합니다.
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  useEffect(() => {
    const timeout = setTimeout(() => {
      setIsVisible(true);
    }, 1000);

    return () => clearTimeout(timeout);
  }, []);

  return (
    <div className={isVisible ? 'slide-in' : ''}>
      <div className="bubbles-container" style={{ '--screen-height': `${screenHeight-100}px` }}>
        <div className="bubbles">
          <span className="purple b3"></span>
          <span className="pink b2"></span>
          <span className="yellow b1"></span>
          <span className="blue b5"></span>
          <span className="green b4"></span>
          <span className="purple b1"></span>
          <span className="pink b5"></span>
          <span className="yellow b3"></span>
          <span className="blue b2"></span>
          <span className="green b4"></span>
          <span className="purple b5"></span>
          <span className="yellow b3"></span>
          <span className="pink b4"></span>
        </div>
      </div>
    </div>
  );
};

export default Bubble;