import TypeIt from "typeit-react";

export default () => {
  
 const getBeforeInit=(instance) => {
   
    instance.options({lifeLike: false,nextStringDelay: 1000,loop: true})
            .pause(2000)
            .delete(41)
            .pause(1000);
            

    // Remember to return it!
    return instance;}
    return (
        <div className="typeit">
            <TypeIt getBeforeInit={getBeforeInit}>
                <span style={{fontSize:'20px', fontWeight:'600', color:'#6c6c6c'}}>안녕하세요 :D</span>
                <br/>
                <span style={{fontSize:'20px', fontWeight:'600', color:'#6c6c6c'}}>Spring을 이용하는 주니어 Backend 개발자입니다.</span>
            </TypeIt>
        </div>
    );
};